.loader-big {
  align-items: center;
  display: flex;
  height: 70px;
  margin: auto;
}

.loader-big .stroke-big {
  animation: animate 0.7s linear infinite;
  background: var(--accent-color);
  border-radius: 50px;
  display: block;
  height: 100%;
  margin: 0 5px;
  position: relative;
  width: 10px;
}

@keyframes animate {

  50% {
    height: 20%;
  }

  100% {
    height: 100%;
  }
}

.stroke-big:nth-child( 1 ) {
  animation-delay: 0s;
}

.stroke-big:nth-child( 2 ) {
  animation-delay: 0.3s;
}

.stroke-big:nth-child( 3 ) {
  animation-delay: 0.6s;
}

.stroke-big:nth-child( 4 ) {
  animation-delay: 0.1s;
}

.stroke-big:nth-child( 5 ) {
  animation-delay: 0.6s;
}

.stroke-big:nth-child( 6 ) {
  animation-delay: 0.3s;
}

.stroke-big:nth-child( 7 ) {
  animation-delay: 0.1s;
}

.loader-small {
  align-items: center;
  display: flex;
  height: 1.75rem;
}

.loader-small .stroke-small {
  animation: animate 0.7s linear infinite;
  background: var(--accent-color);
  border-radius: 50px;
  display: block;
  height: 100%;
  margin: 0 2px;
  position: relative;
  width: 0.2rem;
}

@keyframes animate {

  50% {
    height: 20%;
  }

  100% {
    height: 100%;
  }
}

.stroke-small:nth-child( 1 ) {
  animation-delay: 0s;
}

.stroke-small:nth-child( 2 ) {
  animation-delay: 0.3s;
}

.stroke-small:nth-child( 3 ) {
  animation-delay: 0.6s;
}

.stroke-small:nth-child( 4 ) {
  animation-delay: 0.1s;
}

.stroke-small:nth-child( 5 ) {
  animation-delay: 0.6s;
}

.stroke-small:nth-child( 6 ) {
  animation-delay: 0.3s;
}

.stroke-small:nth-child( 7 ) {
  animation-delay: 0.1s;
}

.hide {
  display: none;
}

.trybetunes {
  align-items: center;
  display: flex;
}

.header-icon {
  margin-right: 0.75rem;
}

.login-container {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 100vh;
  min-width: 100vw;
  position: fixed;
}

.login-wrapper {
  align-items: stretch;
  border: 0.1rem solid;
  border-color: var(--second-color);
  border-radius: 3rem;
  color: var(--accent-color);
  display: flex;
  flex-direction: column;
  padding: 3rem;
}

.login-wrapper:hover {
  box-shadow: 0.2rem 0.2rem 1rem var(--second-color);
}

.login-form {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 24px;
}

.login-form input {
  background-color: var(--transparency);
  border: 0.1rem solid;
  border-color: var(--second-color);
  border-radius: 16px;
  color: var(--accent-color);
  margin-bottom: 1.25rem;
  padding: 0.8rem;
}

.login-form input:-ms-input-placeholder {
  color: var(--second-color);
}

.login-form input::placeholder {
  color: var(--second-color);
}

.login-form input:active {
  border: 0.15rem solid var(--second-color);
  color: var(--second-color);
}

.loginBtn {
  background-color: var(--second-color);
  border: 0.1rem solid var(--second-color);
  border-color: var(--second-color);
  border-radius: 16px;
  color: var(--accent-color);
  padding: 0.8rem;
  transition: 0.7s;
}

.loginBtn:hover {
  background-color: var(--btn-hover);
  border: 0.1rem solid var(--btn-hover);
  transition: 0.7s;
}

.loginBtn:disabled, .loginBtn:disabled:hover {
  background-color: var(--transparency);
  border: 0.1rem solid var(--second-color);
  color: var(--light-color-transparency);
}

