* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}

:root {
  --main-color: #383248;
  --dark-transparency: #3933487e;
  --transparency-card: #13111882;
  --transparency-card-hover: #d8d4f23f;
  --second-color: #A167A5;
  --btn-hover: #7D4B81;
  --accent-color: #D8D4F2;
  --light-transparency: #d8d4f26f;
  --text-light: #d8d4f2d0;
  --text-dark: #1D1A23;
  --music-bg: #1C1924;
  --music-hover: #27232E;
  --transparency: rgba(255, 255, 255, 0);
}

.app-container {
  background: linear-gradient(180.0875052343223deg, rgba(161, 103, 165,1) -2.0545746388443007%,rgba(39, 35, 46,1) 83.33868378812198%);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  left: 0px;
  position: fixed;
  top: 0px;
  width: 100%;
  height: 100%;
  overflow: auto;
  z-index: -1;
} 

input:-webkit-autofill , input:-webkit-autofill:hover
, input:-webkit-autofill:focus , textarea:-webkit-autofill
, textarea:-webkit-autofill:hover , textarea:-webkit-autofill:focus
, select:-webkit-autofill , select:-webkit-autofill:hover
, select:-webkit-autofill:focus {
  background: none;
  border: 0.1rem solid #A167A5;
  border: 0.1rem solid var(--second-color);
  box-sizing: border-box;
  color: var(--text-ligth);
  font-family: 'Montserrat', sans-serif;
  /* font-size: 1rem; */
  -webkit-transition: border 1s;
  -webkit-text-fill-color: #D8D4F2;
  -webkit-text-fill-color: var(--accent-color);
  -webkit-transition: background-color 6000s ease-in-out 0s;
  transition: background-color 6000s ease-in-out 0s;
}
  
input:focus, textarea:focus, select:focus {
outline-offset: 0px !important;
outline: none !important;
}

::selection {
  background-color: #7D4B81;
  background-color: var(--btn-hover);
}

